import React from "react";
import { MDBContainer, MDBRow, MDBCol } from "mdb-react-ui-kit";
import "./Main.css";

export default function App() {
  return (
    <div className="main">
      <MDBContainer>
        <MDBRow className="mb-3 g-2">
          <MDBCol className="cardd-text-center" size="12" lg="12">
            <h2>W CZYM SI SPECJALIZUJ</h2>
          </MDBCol>
        </MDBRow>
        <MDBRow className="g-2">
          <MDBCol
            size="12"
            sm="12"
            md="6"
            lg="3"
            className="cardd rounded-8 shadow-4 p-3"
          >
            <h5 className="cardd-title">Psychoterapia indywidualna</h5>
            <p>
              Psychoterapia jest form pomocy osobom borykajcym si z
              problemami natury psychicznej lub kryzysem yciowym.
            </p>
            <p className="cardd-arrow"></p>
          </MDBCol>
          <MDBCol
            size="12"
            sm="12"
            md="6"
            lg="3"
            className="cardd rounded-8 shadow-4 p-3"
          >
            <h5 className="cardd-title">Konsultacje psychologiczne</h5>
            <p>
              Konsultacje mog by dobrym rozwizaniem dla osb, ktre rozwaaj
              podjcie psychoterapii i zastanawiaj si, jaka forma pomocy
              bdzie w ich przypadku moliwa i skuteczna.
            </p>
            <p className="cardd-arrow"></p>
          </MDBCol>
          <MDBCol
            size="12"
            sm="12"
            md="6"
            lg="3"
            className="cardd rounded-8 shadow-4 p-3"
          >
            <h5 className="cardd-title">Warsztaty dla rodzicw</h5>
            <p>
              Warsztaty dla rodzicw w oparciu o Pozytywn Dyscyplin  metod
              wychowawcz wspierajc rozwj sprawczoci, odpowiedzialnoci i
              samodzielnoci u dzieci w kadym wieku.
            </p>
            <p className="cardd-arrow"></p>
          </MDBCol>
          <MDBCol
            size="12"
            sm="12"
            md="6"
            lg="3"
            className="cardd rounded-8 shadow-4 p-3 cardd-padding"
          >
            <h5 className="cardd-title">Warsztaty dla dzieci i modziey</h5>
            <p>
              Warsztaty profilaktyczne, rozwijajce kompetencje spoeczne,
              wspierajce rozwj emocjonalny  program ustalany w zalenoci od
              potrzeb grupy.
            </p>
            <p className="cardd-arrow"></p>
          </MDBCol>
        </MDBRow>
      </MDBContainer>
    </div>
  );
}
