import "./Omnie.css";
import me from "../assets/me.jpg";


export default function Omnie() {
  return (

    <div className="aboutme">
      <div className="aboutme-primary">
        <div className="aboutme-primary-text">
        <p className="abouttext">
            Jestem psycholok, psychoterapeutk w trakcie caociowego
            4-letniego szkolenia w zakresie terapii poznawczo-behawioralnej,
            czonkiem Polskiego Towarzystwa Terapii Poznawczej i Behawioralnej,
            a take certyfikowan Edukatork Pozytywnej Dyscypliny z akredytacj
            Positive Discipline Association.
          </p>
          <p className="abouttext">
            Jestem absolwentk psychologii ze specjalizacj psychologia zdrowia
            i choroby w Instytucie Psychologii Stosowanej Uniwersytetu
            Jagielloskiego. Ukoczyam te Studium Pedagogiczne Uniwersytetu
            Jagielloskiego.
          </p>
        </div>
        <div className="aboutme-photo">
        <img src={me} alt="that is me" className="photome" />
        </div>
      </div>
      <div className="aboutme-secondary">
      <p className="abouttext">
            Prowadz terapi dzieci, modziey i osb dorosych, wspieram take
            rodzicw dowiadczajcych trudnoci wychowawczych. Pracuj z
            pacjentami z diagnoz zaburze nastroju, lkowych, osobowoci, a
            take osobami LGBT+. W pracy, poza klasycznymi technikami
            poznawczymi i behawioralnymi, wykorzystuj elementy terapii
            dialektyczno - behawioralnej. Regularnie podnosz swoje
            kwalifikacje, a prac terapeutyczn poddaj systematycznej
            superwizji.
          </p>
          <p className="abouttext">
            Dowiadczenie zawodowe zdobywaam midzy innymi w takich miejscach
            jak: Oddzia Psychiatrii Szpitala Specjalistycznego im. J.
            niadeckiego w Nowym Sczu, Oddzia Psychiatrii Dziecicej
            Wojewdzkiego Specjalistycznego Szpitala Dziecicego im. w. Ludwika
            w Krakowie, Stowarzyszenie SIEMACHA czy Poradnia Psychologiczno 
            Pedagogiczna w Nowym Sczu.
          </p>
      </div>
      <div className="aboutme-finish">
      <p className="abouttext">
          Prywatnie jestem pasjonatk grskich wdrwek i dobrej kuchni.
        </p>
      </div>
    </div>
  );
}
